h1, h2, h3, h4, h5, h6, p {
  font-family: Lato;
  font-style: normal;
  color: #19191C;
  margin: 0;
}
h1 {
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
}
h3 {
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
}
h5 {
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
}
h6 {
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
}
p {
  font-size: 10px;
  line-height: 14px;
}

button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  box-sizing: border-box;
  border-radius: 100px;
}

.divider-line {
  height: 1px;
  background: #DFE1E4;
}